<template>
  <div class="login-page">
    <div class="form">
      <div v-if="!register" class="login-form">
        <h2 v-html="$t('login.login')"></h2>
        <br>
        <input type="text" v-model="user.email" :placeholder="$t('login.email')">
        <input type="password" v-model="user.password" @keyup.enter="loginUser" :placeholder="$t('login.password')">
        <p class="text-danger">{{ this.$t(this.loginError)}}</p>
        
        <button v-show="!loading"  @click="loginUser">{{$t('login.login')}}</button>

        <button v-show="!loading"  class="mt-2" @click="loginWithGoogle">{{$t("login.google")}}</button>

        <button v-show="loading" disabled>
          <div class="spinner-border text-light" role="status">
            <span class="sr-only">Loading...</span>
          </div>
        </button>
        <p class="message">
          {{$t('login.not-registered')}}
          <a
            href="#"
            @click.prevent="register = true"
          >{{$t('login.create-new')}}</a>
        </p>
      </div>

      <div v-if="register" class="register-form">
        <h2>{{$t('login.register')}}</h2>
        <br>
        <input type="text" v-model="user.name" :placeholder="$t('login.name')">
        <input type="text" v-model="user.email" :placeholder="$t('login.email')">
        <input type="password" v-model="user.password" @keyup.enter="registerUser"  :placeholder="$t('login.password')">
        <p class="text-danger">{{ this.$t(this.registerError)}}</p>
        <button @click="registerUser" v-show="!loading">{{$t('login.register')}}</button>
        <button v-show="loading" disabled>
          <div class="spinner-border text-light" role="status">
            <span class="sr-only">Loading...</span>
          </div>
        </button>
        <p class="message">
          {{$t("login.already-register")}}
          <a
            href="#"
            @click.prevent="register = false"
          >{{$t("login.do-login")}}</a>
        </p>
      </div>

      <br>
      <img src="../assets/img/turkish.png" @click="changeLang('tr')" class="flag" title="Türkçe">
      <img src="../assets/img/english.png" @click="changeLang('en')" class="flag" title="English">
    </div>
  </div>
</template>


<script>
import firebase from "../plugins/firebase.js";

export default {
  data: function() {
    return {
      register: false,
      loginError: "",
      registerError: "",
      user: { name: "", email: "", password: "" },
      loading: false
    };
  },
  methods: {
    changeLang: function(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem("lang", lang);
    },
    loginWithGoogle() {
      this.loginError = "";
      this.registerError = "";
      this.loading = true;

      // Using a popup.
      let provider = new firebase.auth.GoogleAuthProvider();
      provider.addScope("profile");
      provider.addScope("email");
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(function(result) {
          this.loading = false;
          this.$router.push("/home");
        })
        .catch(error => {
          this.loginError = "error." + error.code;
          this.loading = false;
        });
    },
    loginUser() {
      this.loginError = "";
      this.registerError = "";
      this.loading = true;

      firebase
        .auth()
        .signInWithEmailAndPassword(this.user.email, this.user.password)
        .then(data => {
          this.$router.push("/home");
          this.loading = false;
        })
        .catch(error => {
          this.loginError = "error." + error.code;
          this.loading = false;
        });
    },
    registerUser() {
      this.loginError = "";
      this.registerError = "";
      this.loading = true;

      firebase
        .auth()
        .createUserWithEmailAndPassword(this.user.email, this.user.password)
        .then(firebaseUser => {
          this.$router.push("/home");
          this.loading = false;
        })
        .catch(error => {
          this.registerError = "error." + error.code;
          this.loading = false;
        });
    }
  }
};
</script>

<style>
.spinner-border {
  width: 1rem !important;
  height: 1rem !important;
}
</style>
