import Vue from 'vue'
import i18nVue from 'vue-i18n'
import { tr } from './tr'
import { en } from './en'

Vue.use(i18nVue);


var selectedLang = "tr";

if (localStorage.getItem("lang") != null)
    selectedLang = localStorage.getItem("lang");


export const i18n = new i18nVue({
    locale: selectedLang, fallbackLocale: selectedLang, messages: {
        tr: tr, en: en
    }
});

