<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <button
      class="navbar-toggler"
      type="button"
      data-toggle="collapse"
      data-target="#navbarTogglerDemo01"
      aria-controls="navbarTogglerDemo01"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarTogglerDemo01">
      <a class="navbar-brand" href="#">Firevue</a>
      <ul class="navbar-nav mr-auto ml-auto mt-2 mt-lg-0">
        <router-link tag="li" to="/home" class="nav-item">
          <a class="nav-link" href="#">{{$t("pages.main-page")}}</a>
        </router-link>

        <router-link tag="li" to="/todo" class="nav-item">
          <a class="nav-link" href="#">{{$t("pages.todo-list")}}</a>
        </router-link>

        <li class="nav-item" @click="logout">
          <a class="nav-link text-danger" href="#">{{$t("pages.logout")}}</a>
        </li>
      </ul>
      <div class="form-inline my-2 my-lg-0">
        <img
          src="../assets/img/turkish.png"
          @click="changeLang('tr')"
          class="flag mr-1"
          title="Türkçe"
        >
        <img src="../assets/img/english.png" @click="changeLang('en')" class="flag" title="English">
      </div>
    </div>
  </nav>
</template>

<script>
import  firebase  from "../plugins/firebase.js";

export default {
  methods: {
    changeLang: function(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem("lang", lang);
    },
    logout() {
      firebase.auth().signOut();

      this.$store.state.logged = false;
      this.$router.push("/login");
    }
  }
};
</script>

